@import url('https://fonts.googleapis.com/css?family=IM+Fell+DW+Pica+SC|Pirata+One');

.NoteForm {
  -ms-flex-positive: 1;
      flex-grow: 1;
  padding: 0 3rem;
  background-image: url("http://www.powerpointhintergrund.com/download/grunge-parchment-background-3395");
  background-size: 100% 100%;
}
.NoteForm .form-actions {
  padding-top: 1rem;
  margin-left: -2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
}
.NoteForm .title-text{
  font-family: 'Pirata One', cursive;
  font-size: 35px;
  background: transparent;
}
.NoteForm .body-text{
  font-family: 'IM Fell DW Pica SC', serif;
  font-size: 30px;
  background: transparent;
}
 .NoteForm .demo-toolbar button{
  border: 3px solid rgba(99, 30, 5, 0.8);
  background: rgba(153, 49, 12, 0.6);
} 
.NoteForm .demo-toolbar button:hover{
  border: 3px solid rgba(99, 30, 5, 1);
  background: rgba(153, 49, 12, 0.7);
  -webkit-box-shadow: 0px 3px rgb(45, 12, 11);
          box-shadow: 0px 3px rgb(45, 12, 11);
}
.NoteForm .demo-toolbar button:disabled{
  border: 3px solid rgba(99, 30, 5, 0.8);
  background: rgba(153, 49, 12, 0.2); 
}
.NoteForm button {
  margin-top: 20px;
  border: 7px double rgba(81, 22, 21, 0.8);
  border-radius: 50%;
  background: rgba(101, 16, 10, 0.8);
  padding: 0;
}
.NoteForm button:hover{
  border: 8px double rgba(81, 22, 21, 1);
  background: rgba(101, 16, 10, 1);
  -webkit-box-shadow: 0px 3px rgb(45, 12, 11);
          box-shadow: 0px 3px rgb(45, 12, 11);
}
.NoteForm button i.fa {
  color: rgba(187, 141, 47, 1);
  font-size: 27px;
  margin: 0;
  padding:13px;
}
.NoteForm button i.fa:hover{
  color: rgba(229, 172, 57, 1);
}
.NoteForm form {
  margin: 0 auto;
  max-width: 80rem;
}
input[name="title"] {
  border: none;
  font-size: 150%;
  font-family: "Fauna One";
  color: #302506;
  font-weight: 300;
  width: 100%;
}
input[name="title"] {
  outline: none;
}
input[name="body"]{
  color:#302506;
}
textarea {
  border-color: rgba(48, 37, 6, 0.8);
  width: 100%;
  height: calc(100vh - 185px);
  font-size: 1.3em;
}


