
.Sidebar {
  width: 60px;
  background-color: #2d2116;
  padding: 0.5rem 0; 
  display: -ms-flexbox; 
  display: flex; 
  -ms-flex-direction: column; 
      flex-direction: column; 
  -ms-flex-align: center; 
      align-items: center; }
.Sidebar .logo {
  font-family: "Fauna One";
  color: #666;
  font-size: 3rem;
}
.Sidebar .logo img {
  width: 30px;
  padding-left: 4px;
}

.Sidebar a.new-note {
  margin-top: 2rem;
  position: relative;
  width: 60%;
}
