.Sidebar button {
  background-color: transparent;
  border: 0;
  color: rgba(187, 141, 47, 1);
  cursor: pointer;
  font-size:30px;
  }
.Sidebar button img {
  position: absolute;
  left: 0;
  width: 100%;
  -webkit-transition: opacity 0.25s ease-in-out;
  -o-transition: opacity 0.25s ease-in-out;
  transition: opacity 0.25s ease-in-out; }
.Sidebar button:hover img.outline {
  color: rgba(229, 172, 57, 1);
  opacity: 0; 
}



